<template>
  <div id='manage' @touchmove="cancelSearch" @touchend="cancelSearch">
    <header>
      <div id='back' @touchstart.prevent="back">
        <img id='back-pic' src='static/assets/back.png' alt='back'>
      </div>
      <p :id="msg">{{msg}}</p>
    </header>
    <div id="manage-box">
      <div v-show="!enterFunc" id="search" @touchstart="search">
        <input id="search-input" type="text" placeholder="搜索菜品名称">
        <div id="search-pic"></div>
      </div>
      <div id="manage-entry" v-show="!enterSearch">
        <transition-group name="funcList">
          <div v-for="(item, index) in renderFunc" :key="index" class="function" @touchstart="enter(index)">
            <img :class="imgClass(index)" :src="imgUrl(item.img)">
            <p>{{item.name}}</p>
            <transition name="back">
              <img v-show="enterFunc" class="close" src="static/assets/close3.png" alt="close" @touchstart="leave">
            </transition>
          </div>
        </transition-group>
      </div>
      <div class="manage-detail" :class="{'active': funcIndex === 0}">
        <transition-group name="foodList">
          <div v-for="(item, index) in foodList" v-show="funcIndex === 0" :key="index" class="food-item">
            <p>{{item.name}}</p>
            <div class="delete-button" @touchend.prevent="del"></div>
            <div class="revise-button" @touchend.prevent="rev"></div>
          </div>
        </transition-group>
      </div>
      <div class="manage-detail" :class="{'active': funcIndex === 1}">
        <transition name="addFood">
          <div v-show="funcIndex === 1">
            <div class="food-information" v-show="!success">
              <div class="intro-item">
                <p>名称</p>
                <input class="food-name" type="text" placeholder="请输入菜品名称">
              </div>
              <div class="intro-item">
                <p>价格</p>
                <input class="food-price" type="text" placeholder="请输入菜品价格">
              </div>
              <div class="intro-item">
                <p>介绍</p>
                <textarea class="food-intro" type="text" placeholder="( 选填 ) 请简单介绍一下菜品，会吸引更多同学哦！"></textarea>
              </div>
            </div>
            <div id="imgPart" v-show="!success">
              <div id="main-img">
                <img v-if="loaded" :src="imgUrls[0]" id="main-pic">
                <p v-else id="start" @touchend.prevent="upload">点击添加照片</p>
              </div>
              <div class="pic-bar" :class="{'show-bar': loaded}">
                <div class="bar-content">
                  <div v-for="(item, index) in imgUrls">
                    <div class="fd-pic" @touchend.prevent="focus(index)">
                      <img :src="item">
                    </div>
                  </div>
                </div>
              </div>
              <input id="file" type="file" accept="image/*">
              <div id="delete" @touchend.prevent="delImg"></div>
              <div id="upload" @touchend.prevent="upload"></div>
            </div>
            <input v-show="!success" id="addFood" type="submit" @click.prevent="submit">
            <img v-show='success' id='success' src="static/assets/success.png" alt="success">
            <transition name="prompt"><p v-show="success" id="prompt">添加成功</p></transition>
            <transition name="check"><p v-show="success" id="thanks">感谢您的支持</p></transition>
          </div>
        </transition>
      </div>
    </div>
  </div>
</template>

<script>
  import $ from 'jquery'
  import lrz from '../../node_modules/lrz/dist/lrz.all.bundle'
  export default {
    name: 'manage',
    data () {
      return {
        msg: '管理',
        func: [
          {index: 0, name: '修改信息', img: 'revise'},
          {index: 1, name: '添加菜品', img: 'add'},
          {index: 2, name: '新增公告', img: 'message'}
        ],
        renderFunc: [],
        foodList: [
          {name: '鸡胸肉扒饭'},
          {name: '奥尔良鸡腿饭'},
          {name: '岐山哨子面'},
          {name: '白切鸡饭'},
          {name: '烧鸭饭'}
        ],
        enterSearch: false,
        enterFunc: false,
        funcIndex: -1,
        imgUrls: [],
        loaded: false,
        picIndex: -1,
        success: false
      }
    },
    methods: {
      back: function () {
        this.msg = 'hdjkas'
        this.$router.push({path: '/mine'})
      },
      search: function (e) {
        e.preventDefault()
        e.stopPropagation()
        if (!this.enterSearch) {
          this.enterSearch = true
          $('#search-pic').attr('src', 'static/assets/search.png')
          $('#search-input').focus()
        }
      },
      imgUrl: function (x) {
        // eslint-disable-next-line
        return 'static/assets/' + x +'.png'
      },
      imgClass: function (x) {
        return this.func[x].img
      },
      enter: function (x) {
        event.preventDefault()
        event.stopPropagation()
        if (!this.enterFunc) {
          this.enterFunc = true
          this.funcIndex = x
          this.renderFunc = new Array(this.func[x])
        }
      },
      leave: function () {
        event.preventDefault()
        event.stopPropagation()
        this.enterFunc = false
        this.renderFunc = this.func
        if (this.funcIndex === 1 && this.success) {
          this.reset()
        }
        this.funcIndex = -1
      },
      cancelSearch: function (e) {
        if (this.enterSearch) {
          if (e.target.id !== 'search-input' && e.target.id !== 'search-pic') {
            this.enterSearch = false
            $('#search-pic').attr('src', 'static/assets/search-grey.png')
            $('#search-input').blur()
          }
        }
      },
      del: function (e) {
        e.preventDefault()
        e.stopPropagation()
        alert('delete')
      },
      rev: function (e) {
        e.preventDefault()
        e.stopPropagation()
        alert('revise')
      },
      upload: function () {
        if (this.imgUrls.length === 5) {
          alert('最多上传五张菜品图片')
        } else {
          $('#file').click()
        }
      },
      delImg: function () {
        this.imgUrls.splice(this.picIndex, 1)
        if (this.picIndex === this.imgUrls.length) {
          this.picIndex -= 1
        }
        $('#main-pic').attr('src', this.imgUrls[this.picIndex])
      },
      focus: function (x) {
        $('#main-pic').attr('src', this.imgUrls[x])
        this.picIndex = x
      },
      // delImage: function(index) {
      //   var self = this
      //   var btnArray = ['取消', '确定']
      //   mui.confirm('确定删除该图片?','提示', btnArray, function (e) {
      //     if (e.index == 1) {
      //       vm.imgUrls.splice(index, 1)
      //     }
      //   })
      // },
      // previewImage: function (url) {
      //   var self = this
      //   self.isPreview = true
      //   self.previewImg = url
      // }
      submit: function () {
        var self = this
        $('.food-information input').val('')
        $('.food-information textarea').val('')
        $('#file').val('')
        this.success = true
        setTimeout(function () {
          self.reset()
        }, 2800)
      },
      reset: function () {
        this.imgUrls = []
        this.loaded = false
        this.picIndex = -1
        this.success = false
        this.enterFunc = false
        this.renderFunc = this.func
        this.funcIndex = -1
      }
    },
    watch: {
      imgUrls: function () {
        var self = this
        if (self.imgUrls.length > 0) {
          self.loaded = true
        } else {
          self.loaded = false
        }
      }
    },
    mounted () {
      var self = this
      this.renderFunc = self.func
      self.picIndex = -1
      document.querySelector('#file').addEventListener('change', function (e) {
        var files = e.target.files || e.dataTransfer.files
        if (!files.length) {
          return
        }
        // eslint-disable-next-line
        lrz(files[0], {
          height: 900
        })
          .then(function (rst) {
            var sourceSize = files[0].size / 1024
            var resultSize = rst.fileLen / 1024
            var scale = parseInt(100 - (resultSize / sourceSize * 100))
            alert('文件压缩' + scale + '%')
            self.imgUrls.unshift(rst.base64)
            self.picIndex = 0
            return rst
          })
          .catch(function (err) {
            // 万一出错了，这里可以捕捉到错误信息
            // 而且以上的then都不会执行
            alert('上传失败！请尝试再次上传！')
            console.log(err)
          })
          .always(function () {
            e.target.value = null
          })
      })
    }
  }
</script>

